html, body {
    height: 100%;
    width: 100%;
  }

  .appName {
    font-family: cinzel;
  }

  img[src^= "https://i.ibb.co/Nthpwm1/logo-1.png"] {
    display: block;
    height: 200px;
    background-position: center;
    background-attachment: absolute;
    margin: 0 auto;
    

  }

  .search {
    background-position: center;
    margin: 0 auto;
   display: flex;
    justify-content: center;
    font-size: 30px;
    font-family: cinzel;
    margin-top: 25px;
    margin-left: 500px;
  }

  .search input {
    width: 400px;
    height: 30px;
    font-size: 22px;
    display: flex;
    justify-content: center;
    margin-right: 500px;
  }

 .stockPhotos {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  
  }

  .stockPhotos  img {
    height: 250px;
    width: 394px;
  }

  .title {
    font-family: cinzel;
    display: block;
    text-align: center;
      height: 60px;
      width: 700px;
      line-height: 60px;
      -moz-border-radius: 30px; 
      border-radius: 30px; 
      background-color: #00244c;
      background-position: center;
      background-attachment: absolute;
      color: white;
      margin: 0 auto;
  }

